<template lang="pug">
  .product-asset-dialog
    el-dialog(:title="dialogData.productName",v-model="visible")
      el-form
        .product-asset-dialog-body
          .product-mes
            .mes-title
              h4 
            .mes-body
              table
                tbody
                  tr
                    th 
                    td.product-rate 6.5%
                    th 
                    td 1
                  tr
                    th 
                    td 
                    th 
                    td 
          .now-state
            .mes-title
              h4 
                span {{dialogData.stated[0]}}
                i.icon-batonx.icon-shenpi
            .mes-body
              table
                tbody
                  tr
                    th 
                    td
                      //- span 
                      span.product-rate {{dialogData.accounts[0] | ktCurrency}}
                    th 
                    td
                  tr
                    th 
                    td
                      //- span 
                      span.redeem {{dialogData.accounts[1] | ktCurrency}}
                    th 
                    td
                  tr
                    th 
                    td
                      //- span 
                      span.product-rate {{dialogData.Surplus | ktCurrency}}
                    th 
                    td
          .account-info
            .account-title 
            .account-content
              .account-left
                .account-left-top 
                .account-left-middle
                  span 
                  i
                .account-left-bottom 
              .account-right
                .account-right-top
                  table
                    tbody
                      tr
                        th 
                        td 
                      tr
                        th 
                        td.account-color 1109 2037 8610 201
                      tr
                        th 
                        td 
                .account-right-bottom
                  table
                    tbody
                      tr
                        th 
                        td 
                      tr
                        th 
                        td.account-color 2232 1937 8710 871
                      tr
                        th 
                        td 
      .dialog-footer(slot="footer")
        el-button(type="primary", size="small", @click="goEaa", v-if="!static && disabled") 
        el-button(type="primary", size="small", @click="goEaa", v-if="!static && !disabled",disabled="disabled") 
        el-button(type="gray", size="small", @click="closeDialog", v-if="!static") 
        el-button(type="gray", size="small", @click="closeDialog", v-if="static") 
</template>

<script>
import {
  Form
} from 'element-ui'
import {
  merge
} from 'lodash'
export default {
  props: {
    static: false // 
  },

  components: {
    ElForm: Form
  },

  methods: {
    showDialog(data, boole) {
      this.visible = true
      this.dialogData = merge({}, data)
      this.disabled = boole
    },
    goEaa() {
      this.visible = false
    },
    closeDialog() {
      this.visible = false
    }
  },

  data() {
    return {
      visible: false,
      disabled: '',
      dialogData: {
        productName: 'A',
        assetType: '',
        state: ['', ''],
        company: [],
        accounts: [1235325236, 435346632],
        Surplus: 799978640,
        boole: true,
        stated: ['']
      }
    }
  }
}
</script>

<style lang="scss">
@import './product-asset-dialog.scss';
</style>



